import React from 'react';
import T from 'i18n-react'
import { Link } from 'react-router-dom';
import { Hero, HeroBody, HeroFooter, Container, Box, Columns, Column, Image, Button } from "bloomer";
import FloatLabelnput from "../components/FloatLabelnput";
import FloatLabelPassword from '../components/FloatLabelPassword'
import Footer from "../components/Footer";

import cap, { Redir } from '../lib/capsdk'

import globalStorage from 'react-global-storage';
import alertify from 'alertify.js'

import "../assets/styles/page-login.css";

import logo from "../assets/images/SVG/capitual-logo.svg";

class RecoveredView extends React.Component {

    state = {
        email: '',
        token: '',
        password: ''
    }

    componentDidMount = () => {
        globalStorage.setState('userID', false);
        let qs = new URLSearchParams(this.props.location.search)
        this.setState({
            email: qs.get('email'),
            token: qs.get('token'),
            password: ''
        })
    }

    changePwd = async() => {
        if (this.state.email && this.state.token && this.state.password) {
            try {
                let sess_key = await cap.requestA('members.recoverPassword', {
                    email_or_phone: this.state.email,
                    token: this.state.token,
                    new_password: this.state.password
                })
                if (sess_key) {
                    alertify.success(T.translate("pages.recovered.success"))
                    localStorage.setItem('sess_key', sess_key)
                    this.props.history.push('/dashboard')
                }
            } catch(e) {
                if (e == 'InvalidToken') {
                    alertify.error(T.translate("pages.recovered.tokenError"))
                }
            }
        }
    }

    render() {
        return (
            <Hero className="page-login" isFullHeight>
                <HeroBody>
                    <Container>
                        <Columns isCentered>
                            <Column isSize={4} isMarginless>
                                <Box>
                                    <Columns isMultiline>
                                        <Column isSize="auto">
                                            <Link to="/">
                                                <Image src={logo} isSize="64x64" hasTextAlign="centered" style={{margin: '0 auto'}} />
                                            </Link>
                                        </Column>
                                        <br/>
                                        <Column isSize={12}>
                                            <FloatLabelnput label={ T.translate("pages.recovered.tokenInput") } type="text" value={this.state.token} onChange={(e) => this.setState({ token: e.target.value })} required />
                                        </Column>
                                        <br/>
                                        <Column isSize={12}>
                                            <FloatLabelPassword strength={true} label={ T.translate("pages.recovered.pwdInput") } type="password" value={this.state.password} onChange={(e) => this.setState({ password: e.target.value })} required />
                                        </Column>
                                        
                                        <Column isSize={12}>
                                            <Button isColor="primary" isSize="large" onClick={this.changePwd} isFullWidth>{ T.translate("pages.recovered.submit") }</Button>
                                        </Column>
                                    </Columns>
                                </Box>
                            </Column>
                        </Columns>
                    </Container>
                </HeroBody>
                <HeroFooter>
                    <Footer />
                </HeroFooter>
            </Hero>
        );
    }
}

export default RecoveredView;


